<template>
  <div class="page">
    <div class="page-header">
      <Header :titleVal="title"></Header>
    </div>
    <div class="page-content">
      <form @submit.prevent="formSubmitPhone">
        <van-cell-group>
          <van-field
            v-model="formText.phone"
            name="phone"
            clearable
            icon="arrow"
            placeholder=""
            bind:click-icon="onClickIcon"
          />
          <van-field
            v-model="formText.sms"
            name="sms"
            clearable
            placeholder=""
            use-button-slot
          >
            <van-button slot="button" size="small" type="primary"></van-button>
          </van-field>
        </van-cell-group>
        <div class="btn-cell">
            <van-button type="primary" size="large" form-type="submit" class="login-btn"></van-button>
        </div>  
      </form>
      <div class="login-option-title"></div>
      <div class="login-option-content">
          <span class="login-author" @click="qqLogin"><img src="../assets/qq-icon.png"/></span>
          <span class="login-author" @click="wxLogin"><img src="../assets/wx-icon.png"/></span>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "@/components/Header";
import requestUrl from "@/utils/commonUrl";
import validator from "@/utils/validator";
export default {
  name: "Login",
  components: {
    Header
  },
  data() {
    return {
      title: "",
      formText: {
        phone: "",
        sms: ""
      },
      rules: {
        phone: [
          {
            validator: (rule, value, callback) => {
              if (!value) {
                callback("");
              } else if (/^[1][0-9]{10}$/.test(value)) {
                callback();
              } else {
                callback("");
              }
            }
          }
        ],
        sms: [{ required: true, message: "" }]
      }
    };
  },
  created() {
    this.validator = validator(this.rules, this.formText);
  },
  mounted() {
    
  },
  methods: {
    //
    validate(callback, data) {
      this.validator.validate((errors, fields) => {
        if (errors) {
          fields.forEach(item => {
            this.$toast(item.message);
          });
        }
        callback && callback(errors, fields);
      }, data);
    },
    //
    formSubmitPhone: function() {
      //
      this.validate((errors, fields) => {
        console.log(fields.length);
        if (fields.length == 0) {
          if(this.formText.phone=="15501233759"&&this.formText.sms=="1234"){
            //localStorage.setItem("isLogin","true");
            //vuexations
            this.$store.dispatch("setLoginFn", "true");
            //
            this.$router.push('/');
          }else{
            this.$toast("");
            return false;
          }   
        } else {
          return false;
        }
      });
    },
    //QQ
    qqLogin: function () {
        this.$toast("QQ");
    },
    //
    wxLogin: function () {
        this.$toast("");
    }
  }
};
</script>
<style scoped>
.page-content{
    background: #ffffff;
}
.btn-cell{
    padding: 10px 15px;
}
.login-btn{
    margin-top: 80px;
}
.login-option-title{
    text-align: center;
    margin-top: 120px;
    color: #999999;
    font-size: 16px;
}
.login-option-content{
    margin-top: 60px;
}
.login-author{
    float: left;
    width: 50%;
    text-align: center;
}
.login-author img{
    width: 20%;
}
</style>



