import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
let store = new Vuex.Store({
    // 1. state
    state:{
        isLogin: localStorage.getItem("isLogin"),
        evaluList: ""
    },

    // 2. getters()
    getters:{
        // statestate
        getLoginFn(state){
            return state.isLogin;
        },
        getEvaluListFn(state) {
            return state.evaluList;
        }
    },
    // 3. actions
    // api()
    actions:{
        // 
        // {commit, state}
        // statestate
        // commitmutations 
        // name
        setLoginFn({commit,state}, name){
            // 
            // mutaions
            commit("setLogin", name);
        },
        setEvaluListFn({ commit, state }, name) {
            // 
            // mutaions
            commit("setEvaluList", name);
        },
    },
    // 4. mutations
    mutations:{
        // statestate
        // name
        setLogin(state, name){
            state.isLogin = name;//stateuser
            localStorage.setItem("isLogin",name);
        },
        setEvaluList(state, name) {
            state.evaluList = name;//stateuser
        }
    }
});

export default store;



